<template>
    <div class="my-music">
        <div class="album-slide">
            <div class="album-img" @click="goSetting" title="">
                <img :src="getUserAvator(avator)">
            </div>
            <ul class="album-info">
                <li> <span style="color:#E6A23C;font-size:40px;">{{username}}</span></li>        
                <li></li>   
                <li> <span style="font-size:25px;">{{userSex}}</span></li>
                <li></li>                
                <li> <span style="font-size:25px;">{{birth}}</span></li>   
                <li></li>                             
                <li> <span style="font-size:25px;">{{location}}</span></li>
            </ul>
        </div>
        <div class="album-content">
            <div class="album-title">
                <p></p>
                <span style="color:#F08080;">&nbsp;&nbsp;{{introduction}}</span>
            </div>
            <div class="songs-body">
                <album-content :songList="collectList">
                    <template slot="title"></template>
                </album-content>
            </div>
        </div>
    </div>
</template>

<script>
import {mixin} from '../mixins';
import {mapGetters} from 'vuex';
import {getUserOfid,getCollectOfUserId,songOfSongId} from '../api/index';
import AlbumContent from '../components/AlbumContent';

export default {
    name:'my-music',
    mixins:[mixin],
    components:{
        AlbumContent
    },
    data(){
        return{
            avator: '',       //
            username:'',  //
            userSex:'',   //
            birth: '',  //
            location: '' , //
            introduction:'', //
            collection:[], //
            collectList:[] , // ()
        }
    },
    computed:{
        ...mapGetters([
            'listOfSongs',  //
            'userId',   //
        ])
    },
    mounted(){
        this.getMsg(this.userId);
        this.getColltion(this.userId);
    },    
    methods:{
        getMsg(userId){
            getUserOfid(userId)
                .then(res =>{
                    this.avator = res.avator;
                    this.username = res.username;
                    if(res.sex==0){
                        this.userSex = '';
                    }else if (res.sex==1){
                        this.userSex = '';
                    }
                    this.birth = this.attachBirth(res.birth);
                    this.location = res.location;
                    this.introduction = res.introduction;          
                             
                })
                .catch(err => {
                    console.log(err);
                })
        },
        //
        getColltion(userId){
            getCollectOfUserId(userId).then(res => {
                this.collection = res; 
                //id
                for(let item of this.collection){
                    this.getSongsOfIds(item.songId);
                }
            }).catch(err => {
                console.log(err);
            })
        },
        //id
        getSongsOfIds(id){
            songOfSongId(id).then(res => {
                this.collectList.push(res);
            }).catch(err => {
                console.log(err);
            })
        },
        goSetting(){
          this.$store.commit('setActiveName',name);
          this.$router.push({path: '/setting'});
      },
    }
}
</script>


<style lang="scss" scoped>
@import '../assets/css/my-music.scss';
</style>


