import { likeSongOfName, loginIn,getCollectOfUserId } from "../api/index";
import {mapGetters} from 'vuex';
export const mixin = {
    computed: {
        ...mapGetters([
            'userId',   //id
            'loginIn',  //            
        ])
    },
    methods: {
        //
        notify(title,type){
            this.$notify({
                title: title,
                type: type
            })
        },
        //
        attachImageUrl(srcUrl){ 
            return srcUrl ? this.$store.state.configure.HOST+srcUrl : this.$store.state.configure.HOST+"/img/songListPic/1.jpg";
        },
        //
        getUserAvator(srcUrl){ 
            return srcUrl ? this.$store.state.configure.HOST+srcUrl : this.$store.state.configure.HOST+"/img/AvatorImages/1.png";
        },
        //
        getSingerUrl(srcUrl){ 
            return srcUrl ? this.$store.state.configure.HOST+srcUrl : this.$store.state.configure.HOST+"/img/singerPic/1.jpg";
        },
        //
        getSong(){  //route
            if(!this.$route.query.keywords){
                this.$store.commit('setListOfSongs',[]);
                this.notify('','warning');
            }else{
                likeSongOfName(this.$route.query.keywords).then(res => {
                    if(!res.length){    //reslist
                        this.$store.commit('setListOfSongs',[]);
                        this.notify('','warning');
                    }else{
                        this.$store.commit('setListOfSongs',res);
                    }
                }).catch(err => {
                    console.log(err);
                }) 
            }
        },
        //.   
        replaceLName(str){
            let arr = str.split('-');
            return arr[0];
        },
        //  
        replaceFName(str){
            let arr = str.split('-');
            return arr[1];
        },
        //
        toplay:function(id,url,pic,index,name,lyric){
            this.$store.commit('setId',id);
            this.$store.commit('setUrl',this.$store.state.configure.HOST+"/song/"+url);
            this.$store.commit('setPicUrl',this.$store.state.configure.HOST+pic);
            this.$store.commit('setListIndex',index);
            this.$store.commit('setTitle',this.replaceFName(name));
            this.$store.commit('setArtist',this.replaceLName(name));
            this.$store.commit('setLyric',this.parseLyric(lyric));
            
            this.$store.commit('setIsActive',false); //,
            if(this.loginIn){
                getCollectOfUserId(this.userId).then(res => {
                    for(let item of res){   //,,
                        if(item.songId == id){  //idid
                            this.$store.commit('setIsActive',true);
                            break;
                        }
                    }
                })
            }
        },
        //
        parseLyric(text){
            let lines = text.split("\n");       //
            let patten = /\[\d{2}:\d{2}.(\d{3}|\d{2})\]/g;   
            let result = []; //
            //
            if(!(/\[.+\]/.test(text))){
                return [[0,text]];
            }
            while(!patten.test(lines[0])){  //,test
                lines = lines.slice(1);
            }
            //,, ()+
            for(let item of lines){
                let time = item.match(patten);//
                let value = item.replace(patten,'');// 

                for(let item1 of time){
                    let t = item1.slice(1,-1).split(":");   //,
                    
                    if(value!=''){
                        result.push([parseInt(t[0],10)*60 +parseFloat(t[1]),value]);   //10
                    }
                }
            }
            // --  -- 
            result.sort(function(a,b){
                    return a[0] - b[0];
            });
            return result;
        },
        //
        attachBirth(val){
            return String(val).substr(0,10); // 
            
        }
    }
}


