<template>
    <transition name="slide-face">
        <div class="the-aside" v-if="showAside">
            <h2 class="title"></h2>
             <li>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</li>
            <ul class="menus">
                <li v-for="(item,index) in listOfSongs" :key="index" :class="{'is-play': id==item.id}" 
                    @click="toplay(item.id,item.url,item.pic,item.index,item.name,item.lyric)">
                    {{replaceFName(item.name)}}
                     &nbsp;&nbsp;&nbsp;
                    {{replaceLName(item.name)}}
                </li>
            </ul>
        </div>
    </transition>
</template>




<script>
import {mapGetters} from 'vuex';
import {getCollectOfUserId} from '../api/index';
export default {
    name : 'the-aside',  
    computed: {
        ...mapGetters([
            'showAside', //
            'listOfSongs',  //
            'id',   //id
            'userId',   //id
            'loginIn',  //            
            'isActive', //
        ])
    },
    mounted(){
        let _this = this;
        document.addEventListener('click',function(){
            _this.$store.commit('setShowAside',false)
        },true);
    },
    methods:{
        //.   
        replaceLName(str){
            let arr = str.split('-');
            return arr[0];
        },
        //  
        replaceFName(str){
            let arr = str.split('-');
            return arr[1];
        },
        //
        toplay(id,url,pic,index,name,lyric){
            this.$store.commit('setId',id);
            this.$store.commit('setUrl',this.$store.state.configure.HOST+"/song/"+url);
            this.$store.commit('setPicUrl',this.$store.state.configure.HOST+pic);
            this.$store.commit('setListIndex',index);
            this.$store.commit('setTitle',this.replaceFName(name));
            this.$store.commit('setArtist',this.replaceLName(name));
            this.$store.commit('setLyric',this.parseLyric(lyric));

            this.$store.commit('setIsActive',false); //,
            if(this.loginIn){
                getCollectOfUserId(this.userId).then(res => {
                    for(let item of res){   //,,
                        if(item.songId == id){  //idid
                            this.$store.commit('setIsActive',true);
                            break;
                        }
                    }
                })
            }
        },
        //
        parseLyric(text){
            let lines = text.split("\n");       //
            let patten = /\[\d{2}:\d{2}.(\d{3}|\d{2})\]/g;   
            let result = []; //
            //
            if(!(/\[.+\]/.test(text))){
                return [[0,text]];
            }
            while(!patten.test(lines[0])){  //
                lines = lines.slice(1);
            }
            //,, ()+
            for(let item of lines){
                let time = item.match(patten);//
                let value = item.replace(patten,'');// 

                for(let item1 of time){
                    let t = item1.slice(1,-1).split(":");   //,
                    
                    if(value!=''){
                        result.push([parseInt(t[0],10)*60 +parseFloat(t[1]),value]);   //10
                    }
                }
            }
            // --  -- 
            result.sort(function(a,b){
                    return a[0] - b[0];
            });
            return result;
        },
    }
    
}
</script>

<style lang="scss" scoped>
@import '../assets/css/the-aside.scss';
.title{
    color:#FF9999;

}
.the-aside{
    background-color: Transparent;
    border-radius: 50px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
}
.menus{
    background-color: Transparent;
    color:#FF9999;
}
</style>


